<template>
	<div class="content">
		<form>
			<label>
				<input type="numbers" name="" v-model='tel' placeholder="手机号" >
			</label>
			<label>
				<input :type="showp?'text':'password'" name="" v-model='pas'  placeholder="密码">
				<div class='btn' @click="showp=!showp">
					<img v-if="!showp" class="showp" src="../assets/icon/showp.png">
					<img v-else class='hidep' src="../assets/icon/hidep.png">
				</div>
			</label>
			<input class="sub" type="button" @click="login" name="" value="登录">
		</form>
		<div class="forgetp">
			<router-link to="/">忘记密码？</router-link>	
		</div>
	</div>
</template>
<script type="text/javascript">
import axios from 'axios'
let longinUrl=require('./common/config.js').loginUrl
export default{
	name:'login',
	data(){
		return {
			showp:false,//是否显示密码
			tel:'',
			pas:''

		}
	},
	methods:{
		login (){
			
			let that=this
			console.log(axios)
			// axios.post(longinUrl, {
		 //    phone: that.tel,
		 //    password:that.password
		 //  })
		 //  .then(function (response) {
		 //    console.log(response);
		 //  })
		 //  .catch(function (response) {
		 //    console.log(response);
		 //  });
		 this.$ajax({
      method: 'post',
	      url: longinUrl,
	      data: {
	        phone: that.tel,
		      password:that.password
	      }
	   })
		}
	}
}
</script>
<style lang="scss" scoped="" type="text/css">
.content{
	width: 100%;
	box-sizing: border-box;
	padding: 0 30px;
	form{
		display: block;
		width: 100%;
		label{
			display: block;
			width: 100%;
			height: 114px;
			box-sizing: border-box;
			border-bottom: 2px solid #eeeeee;
			padding: 20px 0;
			display: flex;
			justify-content: space-between;
			input{
				width: 500px;
				height: 72px;
				font-size: 30px;
				color: #222222;
				display: block;
				line-height: 30px;
				border:none;
				outline: none;
			}
		}
		.btn{
			width: 50px;
			height: 100%;
			display: flex;
			justify-content: center;
			align-items: center;
			.showp{
				display: block;
				width: 41px;
				height: 30px;

			}
			.hidep{
				display: block;
				width: 40px;
				height: 36px;
			}
		}
		.sub{
			width: 100%;
			height: 92px;
			border-radius: 46px;
			display: block;
			border:none;
			outline: none;
			line-height: 92px;
			text-align: center;
			font-size: 36px;
			color: #ffffff;
			background:#50a9ef;
			margin-top: 40px;
		}

	}
	.forgetp{
		width: 100%;
		height: 110px;
		display: flex;
		justify-content: flex-end;
		a{
			display: block;
			line-height: 110px;
			font-size: 30px;
			color: #ff6633;
			text-decoration: none;
		}
	}
}
::-webkit-input-placeholder { /* WebKit, Blink, Edge */
   color: #222222;
   font-size: 30px;
}
:-moz-placeholder { /* Mozilla Firefox 4 to 18 */
   color: #222222;
   font-size: 30px;
}
::-moz-placeholder { /* Mozilla Firefox 19+ */
  color: #222222;
  font-size: 30px;
}
:-ms-input-placeholder { /* Internet Explorer 10-11 */
   color: #222222;
   font-size: 30px;
}
</style>


