<template>
	<div class="content">
		<div class='head'>
			<div class='title'>演出</div>
			<router-link class='search' to="/">
				<img src="../assets/icon/search.png">
			</router-link>
		</div>
		<!-- 此处banner图 750*336 -->
		<div class='banner'>
			<mt-swipe :auto="4000">
			  <mt-swipe-item>1</mt-swipe-item>
			  <mt-swipe-item>2</mt-swipe-item>
			  <mt-swipe-item>3</mt-swipe-item>
			</mt-swipe>
		</div>
		<div class='nav'>
			<div class='nav-item active'><span>当前热门</span></div>
			<div class='nav-item' @click="show1 =true"><span>推荐排序</span><img src="../assets/icon/xiala.png"></div>
			<div class='nav-item' @click="show2 =true"><span>全部时间</span><img src="../assets/icon/xiala.png"></div>
		</div>
		<div class='main-i'>
			<div class='main-item'>
				<div class='item-left'>
					<!-- 此处封面尺寸为130*190 -->
					<div class='pic'>
						<img src="">
					</div>
				</div>
				<div class='item-right'>
					<div class='title'>开心麻花经典爆笑舞台剧《乌龙山伯
爵》第52轮</div>
					<div class='times'>2018.01.30-02.11</div>
					<div class='flag'>
						<div class='flag1'><span>售票中</span></div>
						<div class='flag2'><img src="../assets/icon/hot.png"><span>热度值999</span></div>
					</div>
					<div class='price'>
						<p>80-1088</p><span>元</span>
					</div>
					<div class='remark'>“半分钟一次掌声，一分钟一个笑点。”</div>
				</div>
			</div>
		</div>
		<transition name="bg-fade">
			<div class='picker-bg1 picker-bg' v-if="show1" >
				<div class="btn">
					<span @click="show1 =false">取消</span>
					<span>确定</span>
				</div>
				<!-- 按推荐排序 -->
				<mt-picker :slots="slot1" @change="onValuesChange1"></mt-picker>
			</div>
		</transition>
		<transition name="bg-fade">
			<div class='picker-bg2 picker-bg' v-if="show2" >
				<div class="btn">
					<span  @click="show2 =false">取消</span>
					<span>确定</span>
				</div>
				<!-- 时间排序 -->
				<mt-picker :slots="slot2" @change="onValuesChange2"></mt-picker>
			</div>
		</transition>	
		
	</div>
</template>
<script type="text/javascript">

export default {
	name:"home",
	methods: {
    onValuesChange1(picker, values) {
      console.log(picker,values)
      //console.log(picker.getValues())
    },
    onValuesChange2(picker, values) {
      
    },

  },
	data () {
		 return {
      slot1: [
        {
          flex: 1,
          values: ['推荐排序','时间','热度'],
          className: 'slot1',
          textAlign: 'center'
        }
      ],
      slot2: [
        {
          flex: 1,
          values: ['全部时间','今天', '明天', '本周末','一周内','一月内'],
          className: 'slot2',
          textAlign: 'center'
        }
      ],
      show1:false,//第一个筛选的显示隐藏
      show2:false,//第二个筛选的显示隐藏
    };
	}
}
</script>
<style type="text/css" lang="scss" scoped="">
.content{
	width: 100%;
	.head{
		width: 100%;
		height: 88px;
		box-sizing: border-box;
		border-bottom: 2px solid #eeeeee;
		position: relative;
		.title{
			width: 100%;
			line-height: 88px;
			font-size: 36px;
			color: #323232;
			text-align: center;
		}
		.search{
			position: absolute;
			right: 30px;
			top: 0;
			width: 78px;
			height: 88px;
			display: flex;
			align-items: center;
			justify-content: center;
			img{
				width: 42px;
				height: 42px;
				display: block;
			}
		}
	}
}	
.banner{
	width: 100%;
	height: 336px;
	margin: 2px 0 10px 0;
	img{
		display: block;
		width: 100%;
		height: 100%;
	}
}
.nav{
	width: 100%;
	height: 92px;
	box-sizing: border-box;
	border-top:1px solid #f9f9f9;
	border-bottom: 1px solid #f9f9f9;
	display: flex;
	.nav-item{
		height: 100%;
		flex:1;
		display: flex;
		align-items: center;
		justify-content: center;
		span{
			font-size: 26px;
			color: #222222;
		}
		img{
			display: block;
			width: 16px;
			height: 10px;
			margin-left: 10px;
		}
	}
	.active{
		span{
			color: #ff6633;
		}
	}
}
.picker-bg{
	width: 100%;
	height: 100vh;
	position: fixed;
	z-index: 99;
	left: 0;
	top: 0;
	background: rgba(0,0,0,.8);
	.picker{
		position: absolute;
		width: 100%;
		bottom: 0;
		background: #fff;
	}
	.btn{
		position: absolute;
		bottom:180px;/*no*/
		width: 100%;
		height: 60px;
		background: #fff;
		box-sizing: border-box;
		border-bottom: 2px solid #ccc;
		background:  #ccc;
		display: flex;
		justify-content: space-between;
		span{
			display: block;
			width: 100px;
			height: 60px;
			line-height: 60px;
			font-size: 26px;
			color: #000;
			text-align: center;
		}
	}
}
.bg-fade-enter-active {
  transition: all .15s ease;
}
.bg-fade-leave-active {
  transition: all .15s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.bg-fade-enter, .bg-fade-leave-to
/* .slide-fade-leave-active for below version 2.1.8 */ {
  opacity: 0;
}


.main-i{
	width: 100%;
	.main-item{
		width: 100%;
		border-bottom: 2px solid #eee;
		display: flex;
		box-sizing: border-box;
		padding: 0 30px;
		display: flex;
		justify-content: space-between;

		.item-left{
			width: 130px;
			box-sizing: border-box;
			padding-top: 30px;
			img{
				display: block;
				width: 130px;
				height: 190px;
				border-radius: 5px;
				background: #ccc;
				overflow: hidden;
			}
			

		}
		.item-right{
			width: 536px;
			padding-top: 24px;
			.title{
				width: 100%;
				height: 90px;
				line-height: 45px;
				font-size: 32px;
				overflow:hidden; 
				text-overflow:ellipsis;
				display:-webkit-box; 
				-webkit-box-orient:vertical;
				-webkit-line-clamp:2; 
			}
			.times{
				width: 100%;
				height: 66px;
				line-height: 66px;
				font-size: 26px;
				color: #000;
			}
			.flag{
				width: 100%;
				height: 40px;
				box-sizing: border-box;
				padding: 3px 0;
				display: flex;
				.flag1{
					height: 34px;
					box-sizing: border-box;
					padding: 0 8px;
					border:2px solid #50a9ef;
					border-radius: 5px;
					span{
						display: block;
						line-height: 34px;
						font-size: 20px;
						color: #50a9ef;
					}
				}
				.flag2{
					height: 34px;
					box-sizing: border-box;
					padding: 0 8px;
					border:2px solid #da1111;
					border-radius: 5px;
					display: flex;
					align-items: center;
					margin-left: 14px;
					span{
						display: block;
						line-height: 34px;
						font-size: 20px;
						color: #da1111;
						margin-left: 8px;
					}
					img{
						display: block;
						width: 17px;
						height: 25px;

					}
				}
			}
			.price{
				width: 100%;
				height: 58px;
				display: flex;
				align-items:baseline;
				margin-bottom: 13px;
				p{
					display: block;
					line-height: 58px;
					height: 58px;
					font-size: 30px;
					color: #ff6633;
				}
				span{
					height: 50px;
					font-size: 20px;
					color: #717171;
				}
			}
			.remark{
				width: 100%;
				height: 71px;
				line-height: 71px;
				display: block;
				white-space: nowrap;
				overflow: hidden;
				text-overflow: ellipsis;
				font-size: 26px;
				color: #686868;
				box-sizing: border-box;
				border-top:1px solid #f5f5f5;
			}
		}
	}
	

}
</style>


