<template>
	<div>
		<router-view></router-view>
		<div class="foot">
			<router-link to='home' :class="path=='/home'?'active':''">
				<span >
					<img :src="path=='/home'?home1:home0">
				</span>
				<p>首页</p>
			</router-link>
			<router-link to='mine' :class="path=='/mine'?'active':''">
				<span >
					<img :src="path=='/mine'?mine1:mine0">
				</span>
				<p>我的</p>
			</router-link>
		</div>
	</div>
</template>
<script type="text/javascript">
	import home1 from '@/assets/icon/home1.png'
	import home0 from '@/assets/icon/home0.png'
	import mine0 from '@/assets/icon/mine0.png'
	import mine1 from '@/assets/icon/mine1.png'
	export default {
		name:'mains',
		data (){
			return {
				msg:'主页',
				path:'/home',
				home1,
				home0,
				mine1,
				mine0
			}
			
		},
		watch: {
      '$route': 'facthData'
    },
    methods:{
    	facthData() {
    		console.log(this.$route.path)
    		this.path=this.$route.path
    	}
    }
	}
</script>
<style type="text/css" scoped="" lang="scss"> 
.foot{
	width: 100%;
	height: 98px;
	position: fixed;
	left: 0;
	bottom: 0;
	display: flex;
	box-sizing: border-box;
	border-top: 2px solid #d8d8d8;
	background: #ffffff;
	.active{
		p{
			color: #3aa3f0;
		}
	}
	a{
		width: 100%;
		height: 96px;
		text-decoration: none;
		p{
			width: 100%;
			height: 32px;
			line-height: 32px;
			color: #888888;
			text-align: center;

		}
		span{
			width: 100%;
			height: 64px;
			display: flex;
			align-items: center;
			justify-content: center;

		}
	}
	a:nth-of-type(1){
		img{
			width: 56px;
			height: 52px;
			display: block;
		}
	}
	a:nth-of-type(2){
		img{
			width: 43px;
			height: 52px;
			display: block;
		}
	}
}
</style>


