<template>
	<div class="content">

		<div class="content-top">
			<div class="title">某某剧院</div>
			<div class="route">
				<router-link to="/account/login">
					<div>登录</div>
					<p v-if="path=='/account/login'"></p>
				</router-link>
				<span class="bor"></span>
				<router-link to="/account/register">
					<div>注册</div>
					<p v-if="path=='/account/register'"></p>
				</router-link>
			</div>
		</div>
		<router-view></router-view>
	</div>
		
</template>
<script type="text/javascript">
export default{
	name:'account',
	data () {
		return {
			msg:233,
			path:'/account/login'
		}
	},
	watch: {
      '$route': 'facthData'
  },
  methods:{
  	facthData() {
  		console.log(this.$route.path)
  		this.path=this.$route.path
  	}
  }
}
</script>
<style type="text/css" lang="scss" scoped="">
.content{
	width: 100%;
	height: 100vh;
	.content-top{
		position: relative;
		width: 100%;
		height: 352px;
		background: url(../assets/img/loginbg.png) center center no-repeat;
		background-size: 750px 352px;
		box-sizing: border-box;
		padding-top: 20px;
		.title{
			width: 100%;
			height: 234px;
			line-height: 234px;
			font-size: 50px;
			color: #fff;
			text-align: center;


		}
		.route{
			width: 100%;
			height: 100px;
			position: absolute;
			bottom:0;
			left: 0;
			display: flex;
			.bor{
				width: 4px;
				height: 48px;
				display: block;
				background:#ffffff;
			}
			a{
				flex:1;
				height: 100%;
				display: flex;
				flex-direction: column;
				justify-content: space-between;
				align-items: center;
				text-decoration: none;
				div{
					width: 100%;
					height: 48px;
					line-height: 48px;
					color: #fff;
					font-size: 30px;
					text-align: center;
				}
				p{
					width: 0px;
					height: 0px;
					position: relative;
					border-left:18px solid transparent;
					border-right:18px solid transparent;
					border-bottom:18px solid #fff;
				}
			}
		}
	}
}
</style>


